$blue-color: #00A4E5;

%flex-center {
    display: flex;
    align-items: center;
    margin: auto; }

body {
    margin: 0px;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans'; }

p {
    margin: 0;
    line-height: 19px; }

hr {
    width: 50px;
    height: 2px;
    border: none;
    background-color: $blue-color; }

ul {
    padding: 0px;
    .li {
        list-style-type: none;
        padding-top: 12px; } }

.container {
    display: flex;
    margin: auto;
    max-width: 65vw; }

header {
    background-image: url(./src/bg.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    color: white; }

.logo {
    display: flex;
    justify-content: flex-start;

    img {
        margin: auto;
        width: 80%;
        height: 80%;
        position: relative;
        left: 5%;
        bottom: 150%; } }

.centerText {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Bebas Neue', cursive;

    h1 {
        font-size: 60px;
        line-height: 70px;
        letter-spacing: 6px;
        margin: 0; }

    p {
        letter-spacing: 2.5px; } }

.nav {
    background-color: #DADADA;
    height: 65px;
    display: flex; }

.navbar {
    @extend %flex-center;
    justify-content: space-between;
    width: 65vw;

    nav {
        a {
            text-decoration: none;
            margin-left: 30px;
            font-size: 16px;
            font-weight: 500;
            letter-spacing: 1px;
            color: black; } }

    img {
        margin-left: 0px;
        max-width: 100px;
        cursor: pointer; }

    .active {
        color: $blue-color; } }

.about {
    @extend %flex-center;
    flex-wrap: wrap;
    justify-content: space-between;

    h2 {
        width: 100%;
        text-align: center; }

    .storyText {
        width: 40%;
        text-align: left; }

    .storyText h3 {
        font-family: OpenSans;
        font-size: 20px;
        line-height: 23px;
        color: #48555B; }

    .storyText p {
        font-size: 13px;
        line-height: 23px;
        color: #48555B;
        letter-spacing: 0px; }

    .ourImg {
        display: flex;
        align-items: flex-end; }

    .ourImg {
        div {
            margin-left: 20px;
            position: relative; } }

    .ourImg {
        img {
            background-size: cover;
            max-width: 140px;
            max-height: 247px; } }

    .ourImg {
        h3 {
            position: absolute;
            top: 10%;
            left: 10%;
            word-break: break-all;
            font-size: 24px;
            line-height: 29px;
            font-weight: 800;
            color: white;
            margin: 0; } } }

.capabilities {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    max-width: 65vw;
    margin: 120px auto 70px;

    h3 {
        font-size: 18px;
        width: 30%; }

    hr {
        margin-left: 0;
        height: 3px; }

    p {
        font-size: 11px;
        line-height: 19px;
        width: 40%; }

    button {
        background-color: $blue-color;
        color: white;
        cursor: pointer;
        border: none;
        width: 150px;
        height: 40px; } }

.products {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    justify-content: space-between;
    max-width: 65vw;
    margin: auto;
    margin-bottom: 125px; }

.product {
    padding: 24px;
    max-width: 26%;
    height: 260px;
    border: 1px solid #dfdede;
    background-color: #E6E6E6;

    h3 {
        font-size: 16px; }

    p {
        font-size: 13px; } }

.amazing {
    background-color: #171819;
    background-image: url(./src/bg2.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;

    div {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        position: relative;
        top: 20%; }

    button {
        border: none;
        margin-top: 15px;
        padding: 12px;
        cursor: pointer;
        background-color: $blue-color;
        color: white;
        font-size: 13px;
        line-height: 15px;
        letter-spacing: 0.5px; } }

.search {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100px;

    background-color: #171819;
    color: white; }

.search {
    .wrapper {
        display: flex;
        justify-content: space-between;
        text-align: center;
        width: 42vw; } }

.search {
    .wrapper {
        p {
            font-size: 30px;
            line-height: 26px; } } }

.search {
    form {
        display: flex;
        height: 40px; } }

.search {
    input {
        padding: 10px;
        width: 200px;

        border: none;
        font-size: 14px; } }

.search {
    button {
        width: 150px;

        padding: 5px;
        border: none;
        cursor: pointer;

        background-color: $blue-color;
        color: white;
        text-align: center;
        font-size: 13px;
        line-height: 15px; } }

footer {
    background-color: #141A20;
    color: #FFFFFF; }

.wrapperFooter {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    max-width: 70vw;
    margin: auto;
    padding: 30px 0px 30px 0px; }

.wrapperFooter {
    img {
        margin-bottom: 20px; } }

.wrapperFooter {
    .contentAboutUs {
        max-width: 35%; } }

.wrapperFooter {
    .contentLinks {
        display: flex;
        flex-direction: column;
        max-width: 30%; } }

.wrapperFooter {
    h3 {
        position: relative;
        width: 100%; } }

.wrapperFooter {
    .links {
        display: flex;
        justify-content: space-between; } }

.wrapperFooter {
    ul {
        margin: 0;
        padding-right: 34px; } }

.wrapperFooter {
    .contentLogos {
        img {
            margin-top: 19px;
            padding: 10px; } } }
